.sb-theme-purple {
  // Prism Theme Colors
  --sbt-theme-color-10: var(--sbt-purple-10);
  --sbt-theme-color-20: var(--sbt-purple-20);
  --sbt-theme-color-30: var(--sbt-purple-30);
  --sbt-theme-color-40: var(--sbt-purple-40);
  --sbt-theme-color-50: var(--sbt-purple-50);
  --sbt-theme-color-60: var(--sbt-purple-60);
  --sbt-theme-color-70: var(--sbt-purple-70);
  --sbt-theme-color-80: var(--sbt-purple-80);
  --sbt-theme-color-90: var(--sbt-purple-90);
  --sbt-theme-color-100: var(--sbt-purple-100);

  // Header colors
  --sbt-color-header-dark: var(--sbt-theme-color-60);
  --sbt-color-header-darker: var(--sbt-theme-color-70);
  --sbt-color-header-light: var(--sbt-theme-color-40);
  --sbt-color-header-lighter: var(--sbt-theme-color-30);
}
